@import url(https://fonts.googleapis.com/css?family=Alata|Open+Sans|Trade+Winds&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.AppContainer{background-color:lightcoral;display:flex;flex-wrap:wrap}.App{text-align:center}.container{background-color:black;flex:1 1;padding:20px;margin:0}.container h1{padding:0;margin:0;font-family:Trade Winds;font-weight:100;color:white}.section{background-color:red;display:flex;justify-content:space-around;align-items:center;flex-direction:column}.dices-type{background-color:blue;width:50%;display:flex;flex-wrap:wrap}.dices-type img,.dices-type svg{width:calc(100% / 8);padding:10px;box-sizing:border-box}.header-app{display:flex;font-family:Trade Winds;justify-content:space-between;align-items:center;min-height:10vh;padding:0 20px;width:100%;background-color:#262626}.header-app a{color:white;text-decoration:none;font-weight:100;text-transform:uppercase}.menu-hide{display:none}.menu-show{display:block}.menu-links{width:200px;display:flex;padding:0;margin:0;flex-direction:column;justify-content:flex-start;align-items:center;list-style-type:none;text-align:left;height:90vh;background-color:#404040}.menu-links li:first-child{padding-top:20px}.menu-links li{width:90%;padding:4px 0;margin:0}.menu-links a{padding:0;margin:0;color:white;text-decoration:none}.poke-list{display:flex;justify-content:space-between;align-items:center}.w3-table{width:50%;background-color:green}.w3-table input{width:90%}.dice-results{background-color:yellow;padding:5px;margin:0;list-style-type:none}.dice-results li{display:inline;margin:0 5px;background-color:blue}@media (max-width: 900px){.menu-show,.menu-links{width:200px;position:fixed;top:64px}.container{width:100%}.dices-type img,.dices-type svg{width:calc(100% / 4)}}

